//Widget
.widget {
  background: $light-blue;
  margin-bottom: $xl-size
}

//Widget header

.widget-header {
  background: $blue;
  color: $off-light-violet;
  padding: $m-size;
  display: flex;
  justify-content: space-between;
}

.widget-header__title {
  margin: 0
}

.widget__message {
  color: $off-light-violet;
  margin: 0;
  padding: $l-size;
  text-align: center;
  border-bottom: 1px solid lighten($light-blue, 10%);

}